.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.container{
  display: flex;
}

.wrapper {
    padding-top: 50px;
    padding-left: 50px;
    padding-right: 100px;
    padding-bottom: 50px;
    width: 20%;

}

.wrapper fieldset {
    margin: 20px 0;
}

.greenpaper{
  flex-grow: 1;
  padding-top: 50px;
  padding-left: 100px;
  padding-right: 50px;
  padding-bottom: 50px;
  background: grey;
}

.greenButton{
  margin-bottom: 20px;
}